import React, { useEffect } from 'react';
import { useParams, Link } from 'react-router-dom';
import { Cources } from '../../modules/data';
import './PageInfo.css'; //    PageInfo
import welcomeImage from '../../assets/ikonka.svg'; // 

const PageInfo: React.FC = () => {
  const { processId } = useParams<{ processId: string }>(); //   processId  URL
  const courseId = parseInt(processId || '0', 10); //  processId  

  const course = Cources.find(item => item.id === courseId); //    ID

  useEffect(() => {
    window.scrollTo(0, 0); //     
  }, []);

  if (!course) {
    return <div>Invalid process ID</div>; //  ,    
  }

  return (
    <div className="page-info">
      <Link to="/WelcomeCourceApp/" className="back-link"> </Link> {/*  " " */}
      <div className="course-info">
        <h1> <b></b>   <b></b></h1> {/*   */}
        <div className="video-container">
          <iframe
            src="https://www.youtube.com/embed/your-video-id"
            title="YouTube video player"
            frameBorder="0"
            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
            allowFullScreen
          ></iframe>
        </div>
        <h1> </h1> {/*   */}
        <p>           .        ,  ,  , -     .</p>
        <h1> ?</h1> {/*   */}
        <p>  25-    .    :</p>
        <p>( )</p>
        <h1></h1> {/*   */}
        <p className="price">13500&#8381;</p> {/*   */}
      </div>
      <img src={welcomeImage} alt="Ikonka" className="ikonka" />
      <button className="platform-button" onClick={() => window.location.href = 'https://cs11.pikabu.ru/post_img/big/2020/04/18/6/1587203460178710714.png'}>
          
      </button>
    </div>
  );
};

export default PageInfo;
